// Layout Style

@include media-breakpoint-down(xs) {
  main {
    margin-top: 7rem !important;
  }
}
@include media-breakpoint-up(md) {
  #sticky-side {
    &.stickBottom {
      position: fixed;
      bottom: 15px;
      width: calc(41.66667% - 20px);
    }
    &.stickTop {
      position: sticky;
      top: 70px;
    }
  }
  .navbar .sidenav-wrap {
    min-width: 267px;
  }
}

@include media-breakpoint-up(lg) {
  .navbar {
    flex-wrap: nowrap !important;
  }
  #mainWrap {
    max-width: 880px;
    min-width: 880px; 
  }
  .main-wrap {
    max-width: 760px;
    min-width: 450px;
  }
  #sticky-side.stickBottom,
  #stiky-side {
    width: 290px;
  }
}
.cookie-note {
  position: sticky;
  bottom: 0;
}
