// Bootstrap overrides variables

$primary:       #DE6752; // orange
$secondary:     #293C4C; //dark blue
$third:					#31B7FF; //light blue

$link-color:    $third;

$white:    #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black:    #000 !default;


$body-bg:   #F0F2F4;


$spacer: 1rem !default;
$spacer-0: 0;
$spacer-1: ($spacer * .25);
$spacer-2: ($spacer * .5);
$spacer-3: $spacer;
$spacer-4: ($spacer * 1.5);
$spacer-5: ($spacer * 3);

$headings-margin-bottom:      $spacer;

$component-active-bg:         $third;


$btn-font-weight:             600;
$input-placeholder-color:               $gray-500;


//navbar 
$navbar-dark-color:                 $white;
$navbar-dark-hover-color:           rgba($white, .75) !default;
$navbar-dark-active-color:          $white !default;
$navbar-dark-disabled-color:        rgba($white, .25) !default;
$navbar-dark-toggler-icon-bg:       url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'><path stroke='#{$navbar-dark-color}' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/></svg>") !default;
$navbar-dark-toggler-border-color:  rgba($white, .1) !default;

//dropdown
$dropdown-divider-bg:               rgba($gray-200, 0.3);
$dropdown-border-width:             0;


//cards
$card-border-color:                 transparent;
$card-spacer-x:                     1rem;
$card-border-width:                 0;
$card-border-radius:                0.4rem;


$progress-bg:                       $white;


// Popovers
$popover-border-width:              0;
$popover-border-color:              transparent;
$popover-header-bg:                 $white;

//Typo
$font-size-base:              1rem !default; // Assumes the browser default, typically `16px`
$h1-font-size:                $font-size-base * 2.5 !default;
$h2-font-size:                $font-size-base * 2 !default;
$h3-font-size:                $font-size-base * 1.25;
$h4-font-size:                $font-size-base * 1.15;
$h5-font-size:                $font-size-base * 1;
$h6-font-size:                $font-size-base !default;

$text-muted:									#697681;


$badge-font-size:             70%;
$badge-font-weight:           500;

$headings-font-weight:        600;
$headings-color:              $secondary;

//forms
$custom-control-label-color:            $secondary;
$input-bg: 															rgba($secondary, 0.2);
$input-placeholder-color:               rgba($black, 0.56);


//modals

$modal-content-border-color:        transparent;
$modal-content-border-width:        0;


$border-radius-base: 0.25rem;
